.page {
  scroll-behavior: smooth;
  box-sizing: border-box;
}

.page__menu {
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(-100%);
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
  transform: translateX(0);
}

.page__body--with-menu {
  overflow: hidden;
}

h1, h2, h3, h4, h5, h6, p, a, body, li, ul {
  margin: 0;
  padding: 0;
}

@font-face {
  font-family: PlayfairDisplay;
  src: url("PlayfairDisplay-Bold.6bc969fe.ttf");
